









































































import Vue from 'vue';
import Convert from '@/classes/convert';
import SaveData from '@/classes/saveData/saveData';

export default Vue.extend({
  name: 'ShareDialog',
  props: {
    handleClose: {
      type: Function,
      required: true,
    },
  },
  data: () => ({
    loadingURL: false,
    createdURL: '',
    copiedURLHint: '',
    copiedDeckHint: '',
  }),
  computed: {
    deckBuilder(): string {
      const saveData = this.$store.state.mainSaveData as SaveData;
      if (!saveData) {
        return '';
      }
      const manager = saveData.tempData[saveData.tempIndex];
      if (!manager) {
        return '';
      }
      return Convert.createDeckBuilder(manager);
    },
    jervisDeckBuilder(): string {
      const saveData = this.$store.state.mainSaveData as SaveData;
      if (!saveData) {
        return '';
      }
      const manager = saveData.tempData[saveData.tempIndex];
      if (!manager) {
        return '';
      }
      return Convert.createDeckBuilderForJervis(saveData.name, manager);
    },
  },
  methods: {
    close() {
      this.handleClose();
    },
    createURL() {
      this.loadingURL = true;

      setTimeout(() => {
        this.loadingURL = false;
        this.createdURL = '...';
      }, 100);
    },
    copyURL() {
      const textToCopy = document.getElementById('createdURL') as HTMLInputElement;
      textToCopy.select();
      document.execCommand('copy');
      this.copiedURLHint = 'URL';
    },
    copyDeckBuilder() {
      const textToCopy = document.getElementById('deck-builder-text') as HTMLInputElement;
      textToCopy.select();
      document.execCommand('copy');
      this.copiedDeckHint = '';
    },
    clearURLHint() {
      this.copiedURLHint = '';
    },
    clearDeckHint() {
      this.copiedDeckHint = '';
    },
  },
});
